@import "./styles/colors.css";
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap");

@font-face {
	font-family: "main";
	src: url(./fonts/AfrooBold.ttf) format("truetype");
}

* {
	font-family: "main", serif;
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}
html,
body {
	width: 100%;
	height: 100%;
}

#root {
	padding-bottom: 20px;
}

:root {
	--custom-scrollbar-bg: transparent;
	--custom-scrollbar-border: hsl(250, 100%, 75%, 5%);
	--custom-scrollbar-thumb-hover: rgb(109, 80, 252);
	--custom-scrollbar-thumb: #24a3d9;
	overflow: auto;
}

:root::-webkit-scrollbar {
	width: 1.4rem;
	height: 1.4rem;
	background-color: var(--custom-scrollbar-bg);
}

:root::-webkit-scrollbar-track:horizontal {
	border-top: 0.0625rem solid var(--custom-scrollbar-border);
}

:root::-webkit-scrollbar-track:vertical {
	border-left: 0.0625rem solid var(--custom-scrollbar-border);
}

:root::-webkit-scrollbar-corner {
	border-top: 0.0625rem solid var(--custom-scrollbar-border);
	border-left: 0.0625rem solid var(--custom-scrollbar-border);
	background-color: transparent;
}

:root::-webkit-scrollbar-thumb {
	min-width: 3rem;
	min-height: 3rem;
	border-radius: 1rem;
	border: 0.4rem solid transparent;
	background-color: var(--custom-scrollbar-thumb);
	background-clip: content-box;
	-webkit-background-clip: content;
}

:root::-webkit-scrollbar-thumb:hover {
	background-color: var(--custom-scrollbar-thumb-hover);
}

.content__wrapper {
	padding: 30px;
}

body {
	background-color: var(--background);
}

#root {
	direction: rtl;
	color: var(--text);
	background-color: var(--background);
}

.flex {
	display: flex;
}

.justify-center {
	justify-content: center;
}

.items-center {
	align-items: center;
}

.text-muted {
	color: var(--text-muted);
}

.ScrollbarsCustom-Thumb.ScrollbarsCustom-ThumbY {
	background: var(--button) !important;
}

.ScrollbarsCustom-Track.ScrollbarsCustom-TrackY {
	background: var(--scrollbar-bg) !important;
}

.onScreenError {
	position: fixed;
	z-index: 10000000000000;
	width: 100vw;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	backdrop-filter: blur(10px);
	background-color: rgba(50, 50, 50, 0.5);
	.error {
		font-size: 20px;
		padding: 20px 50px;
	}
}

.captcha {
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	margin-bottom: 15px;
}
